  /**
   *  waitfunc 
   *
   * @param  {function} func        
   * @param  {number}   wait        
   * @param  {boolean}  immediate   ture
   * @return {function}             
   */


  module.exports.debounce = function(func, wait = 1000, immediate = true) {

      let timer, context, args;

      // 
      const later = () =>
          setTimeout(() => {
              // 
              timer = null;
              // 
              // 
              if (!immediate) {
                  func.apply(context, args);
                  context = args = null;
              }
          }, wait);

      // 
      return function(...params) {
          // later
          if (!timer) {
              timer = later();
              // 
              // 
              if (immediate) {
                  func.apply(this, params);
              } else {
                  context = this;
                  args = params;
              }
              // later
              // 
          } else {
              clearTimeout(timer);
              timer = later();
          }
      };
  }